// 映射vuex
import popupLayer from '@/components/popup-layer/popup-layer.vue';
import {getYuMing,yh_getStorage} from '@/utils/auth.js'
export default {
	components: {
		//注册为组件标签
		popupLayer
	},
	data() {
		return {
			loginForm:{
				
			},
			username:6221,
			password:123456,
			yuming:yh_getStorage('yuanshiyuming')||'',
			direction: 'top',
			title: 'CRM',
			checked: '',
			flag: 'true',
			showClearIcon: false,
			showPasswordL: false,
			showIp:false,
			isCommit:false
		};
	},
	onShow(){
		console.log(this.$store.state);
		this.$store.dispatch('task/setAddTaskFalg',true)
	},
	methods: {
		// 复选框勾选
		checkboxChange() {
			this.flag = !this.flag;
		},
		// 显示清空账号输入框内容图标
		clearInput: function(event) {
			this.username = event.target.value;
			if (event.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		// 显示清空密码输入框内容图标
		clearpws: function(event) {
			this.password = event.target.value;
			if (event.target.value.length > 0) {
				this.showPasswordL = true;
			} else {
				this.showPasswordL = false;
			}
		},
		clearIp:function(event){
			this.yuming = event.target.value;
			if (event.target.value.length > 0) {
				this.showIp = true;
			} else {
				this.showIp = false;
			}
		},
		// 清空账号或者密码输入框内容
		clearIcon: function(index) {
			if (index == '1') {
				this.username = '';
				this.showClearIcon = false;
			} else if (index == '2') {
				this.userpws = '';
				this.showPasswordL = false;
			}else if(index == '0'){
				this.yuming='';
				this.showIp=false;
			}
		},
		// 弹出层
		togglePopup(direction) {
			this.direction = direction;
			this.$refs.popup.show();
		},
		validate(tipText){
			uni.showToast({
				icon: 'none',
				title:tipText
			});
			this.isCommit=false;
			return;
		},
		// 登入方法
		loginto() {
			this.isCommit=true;
			const {yuming,username,password}=this
			// 前端验证表单
			if (!yuming) {
				this.validate('请输入域名')
			}else if(!username){
				this.validate('请输入用户名')	
			}else if(!password){
				this.validate('请输入密码')
			}else if(!this.flag){
				this.validate('请阅读并同意小A智能客服用户协议')
			}
			// 域名保存本地
			const yuanshiyuming=yuming
			uni.setStorageSync('yuanshiyuming', yuanshiyuming);
			// 域名处理为 https://前缀
			let newyuming=yuming
			if(newyuming.indexOf('https://')==-1){// 用户不输入前缀
				newyuming='https://'+ newyuming;
			}
			uni.setStorageSync('yuming', newyuming);
			
			this.$store.dispatch('user/login',{newyuming,username,password}).then(res=>{
				console.log(res,'登入页请求成功')
				if(res.data.code===1){
					uni.showToast({
						icon: 'none',
						title: "账号或者密码错误"
					});
					this.isCommit=false;
					return;
				}else if(res.data.code===0){
					this.$store.dispatch('user/getinitdata',res.data.data.access_token)		
						console.log(res.data.code)
					uni.switchTab({
						url: '/pages/tabBar/index/index',
					});
				}
			}).catch(err=>{
				this.isCommit=false;
			})
		},
		// 清空input输入框
		empty(type) {
			if (type == 'user') {
				this.username = '';
			} else if (type == 'pws') {
				this.userpws = '';
			};
		},
		
	}
};
